import { mapState, mapActions } from 'vuex'
import SuspendPlay from '../../../components/suspendPlayTemplate'
import { upyunUrl } from '../../../api/config'

export default {
  data () {
    return {
      upyunUrl,
      currentIndex: 0,
      page: 1
    }
  },
  onLoad () {
    this.getShareLatestRankFn()
    this.getShareRankingFn()
  },
  computed: {
    ...mapState([
      'userInfo',
      'shareLatestRankList',
      'shareRankingObj'
    ])
  },
  methods: {
    ...mapActions([
      'getShareLatestRank',
      'getShareRanking'
    ]),
    // 选择类型
    onSelectItemFn (index) {
      this.currentIndex = index
    },
    // 获取上进心列表
    getShareLatestRankFn () {
      const data = {
        that: this,
        page: this.page
      }
      this.getShareLatestRank(data)
    },
    // 获取心榜列表
    getShareRankingFn () {
      const data = {
        that: this,
        page: this.page
      }
      this.getShareRanking(data)
    }
  },
  components: {
    SuspendPlay
  }
}
